.FinishedQuiz {
	margin: 0 10px;
	padding: 20px;
	color: #ffffff;
	border: 2px solid #ffffff;
	border-radius: 5px;
	box-sizing: border-box;
}

.FinishedQuiz ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.FinishedQuiz ul li i {
	margin-left: 10px;
}

.success {
	color: rgba(161, 240, 69, 0.7);
}

.error {
	color: rgba(240, 87, 108, 0.7);
}